<template>
  <div class="film-box" style="height:100%;background-color: #12172B;">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <!--导航-->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <!-- <nav-head :title="title" ></nav-head> -->
        <nav-head :title="languageStatus =='zh' ? '初级认证':'Primary certification'" ></nav-head>
      </div>
      <!-- // 0未认证 1初级认证审核 2初级认证通过 3高级认证审核 4高级认证通过 5初级审核失败 6高级审核失败 -->
      <div class="tipsPosi" v-if="authenticate == 2">
        <img src="../../assets/image/adopt.png" alt="">
        <div class="text">{{$t('common.shenghetongguo')}}</div>
      </div>
      <div class="tipsPosi" v-if="authenticate == 1">
        <img src="../../assets/image/examine.png" alt="">
        <div class="text">{{$t('common.shenghezhong')}}</div>
      </div>
      <div class="tipsPosi" v-if="authenticate == 5">
        <img src="../../assets/image/refuse.png" alt="">
        <div class="text">{{$t('common.shenghejujue')}}</div>
        <div class="userBut" @click="showBox">{{$t('common.chongxintijiao')}}</div>
      </div>
      <div v-if="authenticate == 0">
        <!--分割线-->
        <div class="dividing-line"></div>
        <!-- 认证模块 -->
        <div class="certificationBox">
          <!-- 描述 -->
          <div class="iptBox">
            <span class="span">{{$t('common.xingming')}}</span>
            <input v-if="languageStatus=='en'" class="ipt" v-model="parameter.name" type="text" placeholder="Enter name">
            <input v-else class="ipt" v-model="parameter.name" type="text" placeholder="请输入姓名">
          </div>
          <div class="iptBox">
            <span class="span">{{$t('common.guoji')}}</span>
            <input v-if="languageStatus=='en'" class="ipt" v-model="parameter.country" type="text" placeholder="Enter country">
            <input v-else class="ipt" v-model="parameter.country" type="text" placeholder="请输入国籍">
          </div>
          <div class="iptBox">
            <span class="span">{{$t('common.zhengjianleixing')}}</span>
            <div style="flex: 1;" class="rideoBox">
              <van-radio-group v-model="ctype" direction="horizontal">
              <van-radio name="0">{{$t('common.shenfenzheng')}}</van-radio>
              <van-radio name="1">{{$t('common.huzhao')}}</van-radio>
            </van-radio-group>
            </div>
          </div>
          <div class="iptBox">
            <span class="span">{{$t('common.shenfenzhenghao')}}</span>
            <input v-if="languageStatus=='en'" class="ipt" v-model="parameter.cid" type="text" placeholder="Enter ID number">
            <input v-else class="ipt" v-model="parameter.cid" type="text" placeholder="请输入身份号">
          </div>
        </div>
        <div class="butSub" @click="positiveSub()">{{$t('common.tijiao')}}</div>
      </div>
    </div>
  </div>
</template>

<script>

import { authenticat } from '@/api/user'
import Vue from 'vue'
import NavHead from '@/components/head.vue'
import { Uploader, Toast, RadioGroup, Radio } from 'vant'
import Cryptojs from 'crypto-js'
Vue.use(Uploader).use(Toast).use(RadioGroup).use(Radio)
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      title: '初级认证', // 初级认证
      curHeight: '',
      authenticate: 0,
      ctype: '0',
      parameter: {
        u_id: 2,
        authenticate: 1,
        country: '',
        name: '',
        ctype: '0',
        cid: '',
        front_photo: 0,
        reverse_photo: 0,
        handheld_photo: 0
      },
      radio: '1'
    }
  },
  created () {
    this.parameter.u_id = sessionStorage.getItem('u_id')
    this.authenticate = this.$route.query.authenticate
    this.beforeMount()
    this.getLanguageStatus()
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    positiveSub () {
      var that = this
      console.log('111', this.parameter.ctype)
      if (that.parameter.name === '') {
        that.$toast({
          message: '姓名不能为空',
          duration: 1500
        })
        return
      }
      if (that.parameter.country === '') {
        that.$toast({
          message: '国籍不能为空',
          duration: 1500
        })
        return
      }
      if (that.parameter.cid === '') {
        that.$toast({
          message: '身份证号不能为空',
          duration: 1500
        })
        return
      }
      if (that.ctype === '0') {
        that.parameter.ctype = '身份证'
      } else {
        that.parameter.ctype = '护照'
      }
      var sortToken = that.objKeySort(this.parameter)
      var token = that.sgin(sortToken)
      // console.log(token)
      this.parameter.token = token
      authenticat(this.parameter).then(res => {
        if (res.code === 0) {
          that.$toast({
            message: '提交成功',
            duration: 1500
          })
          that.authenticate = 1
          delete this.parameter.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.isShow = false
        that.addCoin.num = ''
        that.addCoin.paypass = ''
        delete this.addCoin.token
      })
    },
    objKeySort (arys) {
      // 先用Object内置类的keys方法获取要排序对象的属性名，再利用Array原型上的sort方法对获取的属性名进行排序，newkey是一个数组
      var newkey = Object.keys(arys).sort()
      // console.log('newkey='+newkey);
      var newObj = {} // 创建一个新的对象，用于存放排好序的键值对
      for (var i = 0; i < newkey.length; i++) {
        // 遍历newkey数组
        newObj[newkey[i]] = arys[newkey[i]]
        // 向新创建的对象中按照排好的顺序依次增加键值对
      }
      return newObj // 返回排好序的新对象
    },
    sgin (arr) {
      console.log(arr)
      var str = ''
      var hash = ''
      var key = 'MDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
      // eslint-disable-next-line no-undef
      for (var index in arr) {
        // eslint-disable-next-line no-undef
        str += index + '=' + arr[index] + '&'
      }
      var strsgin = str.substring(0, str.length - 1)
      var newstr = strsgin + key
      console.log(newstr, 'newstr')
      hash = Cryptojs.HmacSHA256(newstr, key).toString()
      console.log(hash, '111')
      return hash
    },
    showBox () {
      this.authenticate = 0
    }
  }
}
</script>
<style scoped>
  .content{
    background-color: #000;
    color: white;
  }
  .film-box{
    position: relative;
  }
  .nav-box{
    height: 1.28rem;
    line-height: 1.28rem;
    background-color: #1B213B;
    display: flex;
    justify-content: space-between;
    padding: 0 0.3rem;
    padding-bottom: 0rem;
    color: white;
    font-size: 0.36rem;
  }
  .nav-box img{
    vertical-align: middle;
    flex: 1;
    margin-left: 0.2rem;
  }
  .dividing-line{
    width: 100%;
    height: 0.2rem;
    background-color: #000;
  }
  .certificationBox{
    background: #1B213B;
  }
  .certificationBox .iptBox{
    display: flex;
    background: #1B213B;
    padding-left: 0.3rem;
    border-bottom: 1px solid #12172B;
  }
  .certificationBox .iptBox .span{
    width: 1.2rem;
    line-height: 0.99rem;
    font-size: 0.28rem;
    color: rgba(255, 255, 255, 0.5);
  }
  .certificationBox .iptBox .ipt{
    flex: 1;
    background: #1B213B;
    border: none;
    font-size: 0.28rem;
    margin-left: 0.3rem;
  }
  .butSub{
    margin: 0.3rem 0.3rem 0.6rem 0.3rem;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    background:linear-gradient(-90deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius: 0.1rem;
    color: #ffffff;
  }
  .rideoBox >>> .van-radio-group {
    margin-top: 0.3rem;
    margin-left: 0.3rem;
  }
  .rideoBox >>> .van-radio__label{
    color: rgba(255,255,255,0.5);
    font-size: 0.28rem;
  }
  .tipsPosi{
    width: 5rem;
    height: 5rem;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -2.5rem;
    margin-top: -2.5rem;
  }
  .tipsPosi .img{
    width: 3rem;
    height: 3rem;
    display: block;
    margin: 0 auto;
  }
  .tipsPosi .text{
    text-align: center;
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    margin-top: 0.25rem;
  }
  .userBut{
    border-radius: 0.5rem;
    margin: 0 auto;
    margin-top: 0.3rem;
    background: linear-gradient(-90deg,rgba(16,99,255,1),rgba(2,197,255,1));
    width: 2rem;
    padding: 0.1rem 0.1rem;
  }
</style>
